import React, { Component } from 'react'
import $ from 'jquery'

class RSVP extends Component {
  constructor(props) {
    super(props);

    this.state = { 
      registered: false,
      sending: false
    }

  }

   postToGoogle = (e) => {
    e.preventDefault();
    this.setState({sending: true})
     $.ajax({
       url: "https://docs.google.com/forms/d/e/1FAIpQLSdRmYai8ze-PcJyFQ6mYFiM15jIcnABxJX-O-Gs6LT5XzoJAw/formResponse",
       data: {
         "entry.877086558": $(".rsvp-form input[type='radio'][name='rsvp']:checked").val(),
         "entry.1498135098": $('#names').val(),
         "entry.2606285": $('#spotify').val()
       },
       type: "POST",
       dataType: "xml",
     }).then(() => {
      this.setState({
        registered: true,
        sending: false
      })
     }).catch((e) => {
      this.setState({
        registered: true,
        sending: false
      })
     })
   }
  
  render () {
    return (
      <div>
        <h2>RSVP</h2>
        { !this.state.registered && 
          <form className="rsvp-form" target="_self" onSubmit={this.postToGoogle.bind(this)}>
            
              <p>a mikilvga! Ef  ert hrna inni getum vi ekki mynda okkur gott brkaup n n. Smelltu inn svari a nean hvort  komist og nfnin  eim sem a mta.</p>
              <p>Vi minnum gltlega  a a verur veisla fram  kvld annig a yngra flki (undir 16) arf a vera heima.</p>
              <p>egar a gtarstrengirnir vera allir slitnair mun DJ Spotify taka vi. Ef  situr  heitasta lagi sumarsins  mttu endilega ska eftir v og vi btum v inn  playlistann!</p>
              <fieldset>
              <input id="qs1_op_1" type="radio" value="J, g mti!" name="rsvp" />
              <label for="qs1_op_1">J, g mti!</label>
              <input id="qs1_op_2" type="radio" value="v miur kemst g ekki" name="rsvp" />
              <label for="qs1_op_1">Kemst v miur ekki</label>
            </fieldset>
            <div className='rsvp-form__text'>
              <fieldset>
                <p>Hver eru nfn eirra sem mta?</p>
                <textarea id="names" name="names"></textarea>
              </fieldset>
              <fieldset>
                <p>skalag</p>
                <textarea id="spotify" name="spotify"></textarea>
              </fieldset>
            </div>
              <button id="send" className="btn btn-default" disabled={this.state.sending} type="submit">
                Senda
              </button>
          </form>
        }
        { this.state.registered && 
          <p> ert skrur! Ef  skar svo eftir a breyta skrningunni geturu haft samband vi brhjnin.</p>
        }
        
      </div>
    )
  }
}

export default RSVP



